.menu {
  background-color: #001529;
  padding: 1rem;
  font-size: 1.2rem;
  text-transform: uppercase;
}
  
.menu ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
  
.menu li {
  margin: 0;
  padding: 0;
}
 
.menu-item {
  display: flex;
  align-items: center;
  color: #fff;
  text-decoration: none;
  padding: 1rem;
  border-radius: 0.3rem;
  transition: background-color 0.3s;
}
  
.menu-item:hover {
  background-color: #1890ff;
  color: #001529;
}
  
.menu-item svg {
  margin-right: 0.5rem;
}

@media screen and (max-width: 767px) {
  .menu li {
    padding: 12px;
    font-size: 16px;
  }

  .menu li svg {
    font-size: 18px;
  }

  .menu li span {
    display: inline;
    margin-left: 8px;
  }

  .logout-item {
    font-size: 20px;
    margin: 12px;
  }
}
  